import React from 'react';

import FirstImg from '../../images/Portfolio/site1.jpg';
import Valiosa from '../../images/Portfolio/valiosa.gif';

const Portfolio = () => {
	return (
		<>
			<section id="portfolio" class="light-bg pd-5 pt-5">
				<div class="container">
					<div class="row">
						<div class="col-md-12 ">
							<div class="title font-sec">
								<h3>Featured Projects.</h3>
							</div>
						</div>
					</div>
					<div class="row">
						<div class="col-md-6">
							<div class="portfolio-grid">
								<div class="portfolio-img">
									<img src={FirstImg} alt="My First Site" />
								</div>
							</div>
						</div>
						<div class="col-md-6">
							<div class="portfolio-grid">
								<div class="portfolio-img">
									<img src={Valiosa} alt="My First Site" />
								</div>
							</div>
						</div>
					</div>
				</div>
			</section>
		</>
	);
};

export default Portfolio;
