import React, { useRef, useState, useEffect } from 'react';
import { Link } from 'react-scroll';
//import Logo from '../../images/logo/devlogo.png';
import '../../icofont/icofont.css';

const Navbar = () => {
	const [menuOpen, setMenuOpen] = useState(false);

	const ref = useRef();

	const handleClickOutsideMenu = (e) => {
		if (ref.current.contains(e.target)) {
			setMenuOpen(false);
		}
	};
	useEffect(() => {
		window.addEventListener('mousedown', handleClickOutsideMenu);
		return () => {
			window.removeEventListener('mousedown', handleClickOutsideMenu);
		};
	}, []);

	const onClickMobileMenu = () => {
		setMenuOpen(!menuOpen);
	};

	return (
		<>
			<section id="sidebar">
				<div
					className={`toggle-menu d-sm-block d-md-none ${
						menuOpen ? 'opened' : ''
					}`}
					onClick={onClickMobileMenu}>
					<svg width="40" height="40" viewBox="0 0 100 100">
						<path
							class="line line1"
							d="M 20,29.000046 H 80.000231 C 80.000231,29.000046 94.498839,28.817352 94.532987,66.711331 94.543142,77.980673 90.966081,81.670246 85.259173,81.668997 79.552261,81.667751 75.000211,74.999942 75.000211,74.999942 L 25.000021,25.000058"
						/>
						<path class="line line2" d="M 20,50 H 80" />
						<path
							class="line line3"
							d="M 20,70.999954 H 80.000231 C 80.000231,70.999954 94.498839,71.182648 94.532987,33.288669 94.543142,22.019327 90.966081,18.329754 85.259173,18.331003 79.552261,18.332249 75.000211,25.000058 75.000211,25.000058 L 25.000021,74.999942"
						/>
					</svg>
				</div>
				<div
					className={`sidebar ${
						menuOpen ? 'sidebar-active' : 'sidebar-closed'
					}`}>
					<nav class="menu d-flex align-items-center p-2">
						{/* <h1>
						<a href="index.html" class="logo">
							<img src={Logo} alt="Logo" />
						</a>
					</h1> */}
						<ul ref={ref}>
							<li>
								<Link
									class="d-flex align-items-center flex-column"
									to="home"
									activeClass="active"
									spy={true}
									smooth={true}
									duration={500}>
									<i class="icofont-ui-home mr-3"></i>Home
								</Link>
							</li>
							<li class="">
								<Link
									class="d-flex align-items-center flex-column"
									activeClass="active"
									to="about"
									spy={true}
									smooth={true}
									duration={500}>
									<i class="icofont-briefcase mr-3"></i>About
								</Link>
							</li>

							<li class="">
								<Link
									class="d-flex align-items-center flex-column"
									activeClass="active"
									to="services"
									spy={true}
									smooth={true}
									duration={500}>
									<i class="icofont-tools-alt-2 mr-3"></i>Services
								</Link>
							</li>
							<li class="">
								<Link
									class="d-flex align-items-center flex-column"
									activeClass="active"
									to="portfolio"
									spy={true}
									smooth={true}
									duration={500}>
									<i class="icofont-slidshare mr-3"></i>Projects
								</Link>
							</li>
							<li class="">
								<Link
									class="d-flex align-items-center flex-column"
									activeClass="active"
									to="contact"
									spy={true}
									smooth={true}
									duration={500}>
									<i class="icofont-live-support mr-3"></i>Contacts
								</Link>
							</li>
							<li class="">
								<Link class="d-flex align-items-center flex-column" href="#!">
									<i class="icofont-blogger mr-3"></i>Blog
								</Link>
							</li>
						</ul>
					</nav>
				</div>
			</section>
		</>
	);
};

export default Navbar;
