import React from 'react';

import Backgrnd from '../../images/About/back.jpg';

const HeroSection = () => {
	return (
		<>
			<section
				id="home"
				class="home-banner dark-bg"
				style={{
					backgroundSize: 'cover',
					backgroundRepeat: 'no-repeat',
					backgroundPosition: '0 0',
					backgroundImage: `url(${Backgrnd})`,
				}}>
				<div class="container">
					<div class="row full-screen align-items-center">
						<div class="col ">
							<div class="overlay"></div>
							<div class="type-box">
								<h1 class="font-pri">James Kahoro</h1>
								<h3 class="font-sec">Independent Full-stack Web Developer</h3>
								<p class="desc">
									I am passionate about web development, I love developing
									custom web solutions for clients of all sizes, specializing in
									creating stylish modern websites with reactjs for all types of
									online business and needs.
								</p>
							</div>
						</div>
					</div>
				</div>
			</section>
		</>
	);
};

export default HeroSection;
